const tools = require('zhf.tools'); // 
const applications = require('zhf.applications'); // 
const Super = require('../components-dom-super/g-super'); // ()

// 
const Sub = tools.constructorInherit(Super, {
    wrap: '.g-wrap',
    // 
    callback: {
        // 
        close: function () {
        },
    },
    // 
    config: {
        time: 3000, // 
        isShowIcon: false, // icon
        isShowClose: true, // 
        icon: 'icon-success', // iconclass
        content: '', // 
        positionLocation: 'center', // ('top''center''bottom')positionMethodfixed
    },
    // 
    data: {},
});

// ()()()
Sub.prototype.moduleDomCreate = function () {
    const config = this.opts.config;
    const positionLocation = `g-message_${config.positionLocation}`;// 
    // 
    const html = this.renderAlert();
    this.moduleDom = applications.createElement({
        style: config.moduleDomStyle,
        customAttribute: config.moduleDomCustomAttribute,
        attribute: {
            className: `g-message ${positionLocation}`,
            innerHTML: html,
        },
    });
};

// 
Sub.prototype.renderAlert = function () {
    const config = this.opts.config;
    let htmlIcon = '';
    if (config.isShowIcon) {
        htmlIcon = `<div class="g-message-icon iconfont ${config.icon}"></div>`;
    }
    let closeHtml = '';
    if (config.isShowClose) {
        closeHtml = '<div class="g-message-close iconfont icon-close" ></div>';
    }
    return `
        ${closeHtml}
        ${htmlIcon}
        <div class="g-message-text">${config.content}</div>
    `;
};

// ()()()
Sub.prototype.power = function () {
    const self = this;
    const config = this.opts.config;
    const callback = this.opts.callback;
    const close = this.moduleDom.querySelector('.g-message-close');
    let timer = null;
    timer = setTimeout(function () {
        self.moduleDomHide();
        callback.close();
    }, config.time);
    close.addEventListener('click', function () {
        clearTimeout(timer);
        self.moduleDomHide();
        callback.close();
    });
};

module.exports = Sub;



